.card-wrapper {
  position: relative;
  color: $white-3; }

.game-card.figure {
  position: relative;
  margin-bottom: 0; }

.card-wrapper__links {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: center;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  transition: opacity .3s;
  font-size: 25px; }

.card-wrapper__links:hover {
  opacity: 1; }

.card-wrapper__links div {
  text-align: center;
  flex: 1; }

.card-wrapper__footer {
  display: flex;
  flex-flow: row wrap;
  font-weight: bold; }

.card-wrapper__footer__name {
  width: 100%; }

.card-wrapper__footer__price {
  width: 40%;
  text-align: end; }

.card-wrapper__footer__developer {
  color: $white-5;
  width: 60%; }

.card-wrapper__footer__developer, .card-wrapper__footer__name {
  overflow: hidden;
  text-overflow: ellipsis; }

